import Link from 'next/link'
import React, { Component } from 'react'
import 'isomorphic-fetch'

import Layout from './layout.js'
import Project from './Project.js'

import css from '../style.css'
const API = 'http://localhost:4000/graphql'

export default class Category extends Component {
  constructor() {
    super()

    // console.log('hallooo')

    this.state = {
      draggedProject: null,
      fromCategoryId: ''
    }
  }

  dragStart = (e, draggedProject, fromCategoryId) => {
    console.log(
      'setting state for',
      this.props.category.name,
      draggedProject,
      fromCategoryId
    )
    e.dataTransfer.setData('id', draggedProject.id)
    e.dataTransfer.setData('fromCategoryId', fromCategoryId)
    e.dataTransfer.setData('draggedProject', JSON.stringify(draggedProject))
    // this.setState({ draggedProject, fromCategoryId })
  }

  removeProject = id => {
    console.log('Remove: ', id)
  }

  render() {
    // console.log('the state:', this.state)
    const { category, onDrop } = this.props

    //   <Layout>
    //       </Layout>
    return (
      <div
        className={css.item}
        key={category.id}
        onDragOver={e => e.preventDefault()}
        onDrop={e => {
          setTimeout(() => {
            console.log('From', this.props.category.name, this.state)
            console.log(this.state.draggedProject)
          }, 2000)
          onDrop(
            e,
            category.id,
            this.state.fromCategoryId,
            this.state.draggedProject
          )
        }}
      >
        <div className={css.categoryName}>{category.name}</div>
        {category.projects.map(project => (
          //   <div key={project.id} className={css.project}>
          //       </div>
          <Project
            key={project.id}
            project={project}
            categoryId={category.id}
            dragStart={this.dragStart}
          />
        ))}
      </div>
    )
  }
}

